<template>
  <v-btn
    color="primary"
    :variant="variant"
    :disabled="isInBasket"
    @click.stop="addToBasket"
  >
    {{ isInBasket ? $t('products.added') : $t('products.addToBasket') }}
  </v-btn>
</template>
<script>
import { computed } from "vue";
import { useOrdersStore } from "@/stores/ordersStore";

export default {
  name: "AddToBasketButton",
  props: {
    product: {
      type: Object,
      required: true,
    },
    variant: {
      type: String,
      default: "text",
    },
  },
  setup(props) {
    const isInBasket = computed(() => {
      return useOrdersStore().basket.some(({ id }) => id === props.product.id);
    });

    const addToBasket = () => {
      if (!isInBasket.value) {
        useOrdersStore().addToBasket(props.product);
      }
    };

    return {
      addToBasket,
      isInBasket,
    };
  },
};
</script>
