import { defineStore } from "pinia";

import vendorsMock from "@/mocks/storesMock";

const getAsyncVendors = async () => {
  return new Promise((resolve) => {
    setTimeout(() => {
      resolve({
        data: vendorsMock,
      });
    }, 1000); // Mock 1 second delay
  });
};

export const useVendorsStore = defineStore("vendorsStore", {
  state: () => ({
    vendors: null,
    loading: false,
  }),
  getters: {
    getById: (state) => (id) => {
      return (state.vendors || []).find((vendor) => vendor.id === id);
    },
  },
  actions: {
    async fetchVendors() {
      if (this.vendors || this.loading) return;

      try {
        this.loading = true;
        const response = await getAsyncVendors();
        this.vendors = response.data;
      } catch (err) {
        console.error("Failed to load vendors", err);
      } finally {
        this.loading = false;
      }
    },
  },
});
